"use client";

import { useSocket } from "@/hooks/use-socket";
import { useEffect } from "react";

export default () => {
	const {
		connect,
		disconnect,
		connected,
		join,
		selectFriendships,
		friendships,
	} = useSocket();
	const userId = "user-id-here";

	useEffect(() => {
		connect();
		join(userId);
		selectFriendships(userId);
    console.log(friendships)
		return () => disconnect();
	}, []);

	return <div>Connected: {connected ? "Yes" : "No"}</div>;
};
